.hero {
  max-width: 1360px;
  text-align: center;
  padding: 280px 0;
  margin: 0 auto;
  background-color: #5e5e5e;
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.4),
      rgba(0, 0, 0, 0.4)
    ),
    url(../images/hero/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.hero-title {
  font-weight: 300;
  font-size: 55px;
  line-height: 1.2;
  text-align: center;
  color: #e9e9e9;
  margin-bottom: 25px;
}

.hero-text {
  font-size: 22px;
  line-height: 1.67;
  text-align: center;
  color: #e9e9e9;
  margin-bottom: 29px;
}

.hero-btn {
  @extend .button;

  background-color: var(--accent-color);
  color: var(--secondary-text-color);

  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;

  padding: 10px 30px;
}

.button-icon {
  margin-right: 5px;
}
